/**=====================
    57. breadcrumb CSS Start
==========================**/
.breadcrumb-colored{
	padding: 12px 16px;
	border-radius: 4px;
	li{
		a{
			color: $white;
		}
		&.active{
			color: $white;
			opacity: 0.7;
			font-weight: 300;
		}
	}
	.breadcrumb-item{
		&:before{
			color: $breadcrumb-ancher-color;
			
		}
	}
	.breadcrumb-item.txt-dark{
		&:before{
			color: $breadcrumb-ancher-dark-color;
		}
	}
}
.breadcrumb-icon {
	--bs-breadcrumb-divider: '>';

	li {
		display: inline-block;
	}
}

.breadcrumb-no-divider {
	--bs-breadcrumb-divider: '';
}
.breadcrumb-item{
	&.active{
		text-transform: capitalize;
	}
}

.default-bootstrap-breadcrumb-section{
	.card-body{
		ol{
			&:last-child{
				margin-bottom:-2px;
			}
		}
	}
}
/**=====================
    57. breadcrumb CSS ends
==========================**/
