  /**=====================
55. theme layout CSS Start
==========================**/

  body {
    &.dark-only {
      color: $dark-all-font-color;
      background-color: $dark-body-background;

      .activity-media {
        .media {
          .media-body {
            span {
              color: $dark-small-font-color;
            }
          }

          +.media {
            border-top: 1px solid $dark-card-border;
          }
        }
      }

      .alert-notify-section{
        .card-footer{
          .btn-light{
            color: $dark-card-background!important;
          }
        }
      } 


      // deafult dashboard page css  start 

      .default-dashboard-section,.ecommerce-dashboard-section{
        .earning-card {
          .earning-content {
             h4,h6{
              color: $dark-all-font-color;
            }

           }
        }

        .news {
          .news-update {
            .media-body{
              h5{
                color: $dark-all-font-color;
              }
            }
          }
        }

        .ongoing-project-sec {
          .ongoing-project {
            .card-body {
              .ongoing-project-table.table-responsive {
                table{
                  thead{
                    background-color: $dark-body-background; 
                    span{
                      color: $white;
                    }
                  }
                  tbody {

                    tr {
                      td {
                        h6{
                          color: rgba($dark-all-font-color, 0.7);
                        }
                      }
                    }
                  }
                }
            }
            }
          }
        }

        .recent-activity-sec {
          .recent-activity {
            .media {
              .media-body {
                h5,h6{
                  color: $dark-all-font-color; 
                }
              }
            }
          }
        }

        .total-transactions-sec {
          .report-sec {
              border-color: $dark-card-border;
            .report-main{
               border-color: $dark-card-border !important;
            }

            .report-content {
              h5{
                color: $dark-all-font-color; 
              }
            }
            .report-perfom{
              border-color: $dark-card-border !important;
            }
          }
        }

        .sales-state-sec {
          .sales-small-chart{
            h6{
              color: $dark-all-font-color;  
            }
          }
        }


      }

      // deafult dashboard page css end 

      // =======================  state color page   ======================= //

      .colors-palette-section {
        >.card-body {
          .card {
            .card-body {
              div.color-name {
                background-color: $dark-body-background;

                h6 {
                  color: $light-1;
                }
              }
            }
          }
        }
      }

      .page-wrapper {
        .page-title {
          .breadcrumb {
            .breadcrumb-item {
              a {
                background-color: $dark-card-background;
              }
            }
          }
        }
      }
      
      .page-wrapper {
        .page-header {
          .header-wrapper {
            .nav-right {
              .resp-serch-input {
                .search-form {
                  input {
                    background-color: $dark-card-background;
                    border-color: $dark-card-border;
                    color: $white;
                  }
                }
              }

              .cart-dropdown {
                li {
                  .media {
                    .cart-quantity{
                      border-color: $dark-card-border;

                      input{
                        color: $dark-all-font-color;
                      }

                      span{
                        i{
                        color: $dark-all-font-color;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }

      .button-builder-wrap{
        .btn-light{
          color: $theme-body-font-color !important;
        }
      }
      
      .customizer-links {
        >.nav {
          background-color: $dark-body-background;
        }
      }

      .toast {
        overflow: hidden;
        background-color: $dark-body-background;

        .toast-header {
          background-color: $dark-body-background;
          color: $dark-all-font-color;
          border-top-left-radius: unset;
          border-top-right-radius: unset;
          border-bottom: 1px solid $dark-card-border;
        }

        .toast-body {
          background-color: $dark-body-background;
          color: $dark-all-font-color;

          .border-top {
            border-color: $dark-card-border !important;
          }
        }
      }

      .progress-showcase {
        .progress {
          background-color: $light-1;
        }
      }

      .custom-profile {
        .card-social {
          .social-link {
            background-color: $dark-body-background;
            border: 1px solid $dark-card-border;
          }
        }
      }

      .apexcharts-tooltip.light {
        .apexcharts-tooltip-title {
          background: $dark-card-background;
          color: $white;
        }
      }

      .card {
        background-color: $dark-card-background;
      }

      .ecommerce-widget {
        border: 1px solid $dark-card-border !important;
      }

      .btn-light {
        color: $dark-all-font-color !important;
      }

      .form-select {
        color: $dark-all-font-color;
      }

      .add-project {
        .text-inherit {
          color: $dark-all-font-color;
        }
      }

      .knowledgebase-search {
        color: $theme-body-font-color;

        .form-control-plaintext {
          color: $theme-body-font-color;
        }
      }

      .btn-close {
        filter: brightness(0.8) invert(1);
      }

      .prooduct-details-box {
        .media {
          border: 1px solid $dark-card-border;
        }
      }

      .product-price {
        del {
          color: $dark-small-font-color;
        }
      }

      .ProfileCard {
        border: 1px solid $dark-card-border !important;
      }

      .form-control-plaintext {
        color: $dark-all-font-color;
      }

      .form-select {
        background-color: $dark-body-background;
        border: 1px solid $dark-border-color;
      }

      .box-layout {
        background-color: $dark-body-background;

        .page-wrapper,
        &.page-wrapper {
          .page-header {
            .header-wrapper {
              border: 1px solid $dark-border-color;
            }
          }
        }
      }

      .left-side-header{
        .input-group {
          span{
            border-color: $dark-card-border!important;
          }
          input{
            border-color: $dark-card-border!important;
            color: $white;
          }
        }
      }
      .form-check-input {
        background-color: $dark-body-background;
      }

      .switch-state {
        &:before {
          background-color: $dark-card-background;
        }
      }

      // daterangepicker css start
      .daterangepicker {
        &:before {
          border-bottom-color: $dark-card-background;
        }

        .ranges {
          li {
            background-color: $dark-body-background;
            border-color: $dark-card-background;
            color: $dark-gray;
          }
        }

        .daterangepicker_input {
          .form-control {
            background-color: $dark-body-background;
            border-color: $dark-card-background !important;
          }
        }

        .calendar-time {
          select {
            border-color: $dark-card-background;
            background-color: $dark-body-background;
          }
        }

        &.dropdown-menu {
          background-color: $dark-card-background;
          -webkit-box-shadow: 0 0 14px $dark-body-background;
          box-shadow: 0 0 14px $dark-body-background;
        }

        .calendar-table {
          background-color: $dark-card-background;
          border-color: $dark-card-background;
        }

        &.ltr {
          td {

            &.in-range,
            &.off {
              background-color: $dark-body-background;
              color: $white;
            }
          }
        }
      }

      // daterangepicker css end
      // tooltip css start
      // tooltip css start
      .tooltip {
        &.bs-tooltip-top {
          .tooltip-arrow {
            &:before {
              border-top-color: $dark-card-inbox;
            }
          }
        }

        &.bs-tooltip-bottom {
          .tooltip-arrow {
            &:before {
              border-bottom-color: $dark-card-inbox;
              border-top-color: $transparent-color;
            }
          }
        }

        &.bs-tooltip-start {
          .tooltip-arrow {
            &:before {
              border-left-color: $dark-card-inbox;
              border-top-color: $transparent-color;
            }
          }
        }

        &.bs-tooltip-end {
          .tooltip-arrow {
            &:before {
              border-right-color: $dark-card-inbox;
              border-top-color: $transparent-color;
            }
          }
        }

        .tooltip-inner {
          background-color: $dark-card-inbox;
          color: $white;
        }

        .tooltip-arrow {
          &:before {
            border-top-color: $dark-card-inbox;
          }
        }
      }

      // tooltip css end

      .page-wrapper {
        &.only-body {
          .page-body-wrapper {
            .page-title {
              >.row {
                .col-6 {
                  &:first-child {
                    h6 {
                      color: rgba($dark-all-font-color, 0.5);
                    }
                  }
                }
              }
            }
          }

        }

        .page-header {
          .header-wrapper {
            .nav-right {
              .profile-dropdown li span {
                color: $white;
              }

              .flip-card .flip-card-inner {
                .back {
                  background-color: $dark-card-background !important;

                  .flip-back-content {
                    input {
                      background-color: $dark-body-background;
                      border: 1px solid $dark-card-border;
                      color: $dark-all-font-color;
                    }
                  }

                  li {
                    &:last-child {
                      border-top: 1px solid $dark-card-border;
                    }
                  }
                }

                .front {
                  background-color: $dark-body-background !important;

                  .bookmark-dropdown{
                    li{
                      .bookmark-content{
                        .bookmark-icon{
                          border-color: $dark-card-border;
                          svg{
                            stroke: $dark-all-font-color;
                          }
                        }
                        &:hover{
                          svg{
                            stroke:  var(--theme-deafult);
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }

      .customizer-contain {
        color: $dark-all-font-color;
        background-color: $dark-card-background;

        li {
          &.active {
            box-shadow: 0 0 11px 5px rgba(29, 30, 38, 1);
          }
        }

        .customizer-header {
          border-color: $dark-card-border;
        }

        .customizer-body {

          .sidebar-type,
          .sidebar-setting {
            >li {
              border-color: $dark-card-border;
            }
          }

          .layout-grid {
            li {
              border-color: $dark-card-border;

              .header {
                background-color: $dark-body-background !important;
              }

              .body {
                background-color: $dark-card-background !important;

                ul {
                  background-color: $dark-card-background;

                  li {
                    background-color: $dark-body-background !important;
                  }
                }
              }
            }

            &.customizer-mix {

              .color-layout[data-attr="light-only"],
              [data-attr="dark-sidebar"] {
                background-color: $white;

                .header {
                  background-color: $light !important;
                }

                .body {
                  background-color: $light !important;

                  ul {

                    background-color: $white;

                    li {
                      background-color: $light !important;
                    }
                  }
                }
              }

              .color-layout[data-attr="dark-sidebar"] {
                .body {
                  ul {
                    .sidebar {
                      background-color: $dark-card-background !important;
                    }
                  }
                }
              }

              .color-layout[data-attr="dark-only"] {
                border-color: $dark-card-border;
              }

            }
          }

        }

      }

      .login-card {
        .btn-showcase .btn {
          background-color: $dark-card-background !important;
          color: #fff;
          border-color: $dark-card-background !important;
        }

        .login-main {
          box-shadow: 0 0 37px rgba($white, 0.05);

          .theme-form {
            input {
              background-color: $dark-card-background !important;
            }

            .or {
              &:before {
                background-color: $dark-card-background;
              }
            }

            .checkbox label::before {
              background-color: $dark-card-background !important;
              border-color: $dark-card-background !important;
            }
          }
        }
      }

      .H_ui {
        ~div {
          background-color: $dark-body-background !important;
        }
      }

      .swal-icon--success:after,
      .swal-icon--success:before {
        background: $dark-card-background;
      }

      .swal-icon--success__hide-corners {
        background-color: $dark-card-background;
      }

      .note-editor.note-frame {
        .note-status-output {
          border-top: 1px solid $dark-card-background;
        }

        .note-statusbar {
          border-top: 1px solid $dark-card-background;

          .note-resizebar {
            background-color: $dark-card-background;
          }
        }
      }

      .light-font {
        color: rgba(255, 255, 255, 70%);
      }

      .page-link {
        background-color: $dark-body-background;
        border: 1px solid $dark-card-border;
      }

      .b-r-light {
        border-right: 1px solid $dark-card-border !important;
      }

      .history-details {
        .media {
          border-top: 1px solid $dark-card-border;
        }
      }

      .modal-header {
        border-bottom: 1px solid $dark-card-border;
      }

      #right-history {
        background-color: $dark-card-background;
        box-shadow: 0 0 9px $dark-body-background;

        h6 {
          span {
            a {
              color: $dark-all-font-color;
            }
          }
        }
      }

      .ProfileCard {
        &:hover {
          color: $dark-all-font-color;
          background-color: $dark-card-background;
        }
      }

      .translate_wrapper.active .more_lang {
        &:before {
          border-bottom: 7px solid $dark-card-background;
        }

        .lang {
          border-bottom: 1px solid $dark-body-background;
        }
      }

      .toggle-sidebar {
        svg {
          stroke: var(--theme-deafult);
        }
      }

      .page-wrapper {
        .page-body-wrapper {
          background-color: $dark-body-background;

          .page-title {
            background-color: transparent;
          }
        }

        .sidebar-main-title {
          p {
            color: $dark-all-font-color;
          }
        }

        &.compact-wrapper {
          .page-body-wrapper {
            div.sidebar-wrapper {
              background-color: $dark-card-background;

              .sidebar-main {
                .sidebar-links {
                  .sidebar-link.active {
                    svg {
                      stroke: var(--theme-deafult);
                    }
                  }

                  li {
                    a {
                      span {
                        color: $dark-all-font-color;
                      }

                      svg {
                        stroke: $dark-all-font-color;
                      }
                    }
                  }

                  .simplebar-wrapper {
                    .simplebar-mask {
                      .simplebar-content-wrapper {
                        .simplebar-content {
                          >li {
                            .sidebar-link {
                              &.active {
                                background-color: $dark-body-background;

                                svg {
                                  color: var(--theme-deafult);
                                  stroke: var(--theme-deafult);
                                }
                              }
                            }

                            .mega-menu-container {
                              .mega-box {
                                .link-section {
                                  .submenu-title {
                                    h5 {
                                      color: $dark-all-font-color;
                                    }
                                  }

                                  .submenu-content {
                                    &.opensubmegamenu {
                                      li {
                                        a {
                                          color: $dark-all-font-color;
                                          font-weight: 400;
                                        }
                                      }
                                    }
                                  }
                                }
                              }

                              &::after {
                                background-color: $light-all-font-color;
                              }
                            }


                          }
                        }
                      }
                    }
                  }

                  .sidebar-list {
                    &:hover {
                      >a {
                        &:hover {
                          background-color: $dark-body-background !important;
                        }
                      }
                    }

                    ul.sidebar-submenu {
                      li {
                        a {
                          span {
                            color: $dark-all-font-color;
                          }

                          &.active {
                            color: var(--theme-deafult);
                          }
                        }
                      }
                    }
                  }
                }
              }
            }

            .according-menu {
              i {
                color: $dark-all-font-color;
              }
            }
          }
        }

        &.horizontal-wrapper {
          .page-body-wrapper {
            .sidebar-wrapper {
              background: $dark-card-background;
              border-top: 1px solid $dark-card-border;

              .sidebar-main {
                .sidebar-links {
                  .simplebar-wrapper {
                    .simplebar-mask {
                      .simplebar-content-wrapper {
                        .simplebar-content {
                          >li {
                            >a {
                              color: $dark-all-font-color;
                            }

                            .sidebar-submenu {
                              background: $dark-card-background;

                              li {
                                a {
                                  color: $dark-all-font-color;

                                  &.active,
                                  &:hover {
                                    color: var(--theme-deafult);
                                  }
                                }

                                .nav-sub-childmenu {
                                  background: $dark-card-background;
                                }
                              }
                            }

                            .mega-menu-container {
                              background: $dark-card-background;

                              .mega-box {
                                .link-section {
                                  .submenu-content {
                                    li {
                                      a {
                                        color: $dark-all-font-color;
                                      }
                                    }
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }

                .left-arrow,
                .right-arrow {
                  background-color: $dark-card-background;
                  color: $dark-all-font-color;
                }
              }
            }
          }
        }

        &.compact-sidebar {
          .page-body-wrapper {
            div {
              &.sidebar-wrapper {
                >div {
                  background: $dark-card-background;
                }

                .sidebar-main {
                  .sidebar-links {
                    li {
                      .sidebar-title {
                        border-bottom: 1px solid $dark-card-border;
                      }

                      a {
                        span {
                          color: $dark-all-font-color;
                        }

                        svg {
                          stroke: $dark-all-font-color;
                        }
                      }

                      .sidebar-submenu,
                      .mega-menu-container {
                        background-color: $dark-card-background;

                        li {
                          a {
                            border-bottom: 1px solid $dark-card-border;
                          }
                        }
                      }
                    }

                    .simplebar-wrapper {
                      .simplebar-mask {
                        .simplebar-content-wrapper {
                          .simplebar-content {
                            >li {
                              .sidebar-link {
                                &.active {
                                  background-color: rgba($primary-color, 0.2);

                                  span {
                                    color: var(--theme-deafult);
                                  }

                                  svg {
                                    color: var(--theme-deafult);
                                    stroke: var(--theme-deafult);
                                  }
                                }
                              }

                              .mega-menu-container {
                                .mega-box {
                                  .link-section {
                                    .submenu-title {
                                      h5 {
                                        color: $dark-all-font-color;
                                      }
                                    }

                                    .submenu-content {
                                      &.opensubmegamenu {
                                        li {
                                          a {
                                            color: $dark-all-font-color;
                                            font-weight: 400;
                                          }
                                        }
                                      }
                                    }
                                  }
                                }

                                &::after {
                                  background-color: $light-all-font-color;
                                }
                              }

                              .sidebar-submenu {
                                li {
                                  a {
                                    color: $dark-all-font-color;
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }

                    .sidebar-list {
                      ul.sidebar-submenu {
                        li {
                          a {
                            span {
                              color: $dark-all-font-color;
                            }

                            &.active {
                              color: var(--theme-deafult);
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }

            .according-menu {
              i {
                color: $dark-all-font-color;
              }
            }
          }
        }

        &.material-type {
          .page-header {
            .header-wrapper {
              border-radius: 15px 15px 0 0;
            }
          }
        }

        // material icon css
        &.material-icon {
          .page-header {
            .header-wrapper {
              background-color: $transparent-color;

              .nav-right {
                .language-nav {
                  .more_lang {
                    color: $white;
                  }
                }

                .onhover-show-div {
                  color: $white;
                }

                .flip-card {
                  .flip-card-inner {
                    .back {
                      .flip-back-content {
                        input {
                          border: 1px solid $dark-border-color;
                          background: $dark-body-background;
                          color: $white;
                        }
                      }
                    }
                  }
                }
              }
            }

            .header-logo-wrapper {
              background-color: $transparent-color;
            }
          }

          .page-body-wrapper {
            .page-body {
              background-color: $transparent-color;
            }
          }
        }

        &.advance-layout {
          background-color: $dark-body-background;

          .page-header {
            .header-wrapper {
              background: $transparent-color;

              .header-logo-wrapper {
                background: $transparent-color;
              }
            }
          }

          .page-body-wrapper {
            background: $transparent-color;

            .page-body {
              background: $transparent-color;
            }
          }
        }


      }

      ::-webkit-calendar-picker-indicator {
        filter: invert(1);
      }

      .kanban-board-header {
        background-color: $dark-body-background !important;
        border-bottom: 1px solid $dark-card-background !important;
      }

      .kanban-board .kanban-drag {
        background: $dark-body-background !important;
      }

      .kanban-container {
        .kanban-item {
          .kanban-box {
            border: 1px solid rgba($primary-color, 0.15);
            background-color: $dark-body-background;
          }
        }
      }

      .project-box {
        border: 1px solid rgba($primary-color, 0.15);
        background-color: $dark-body-background;
      }

      .file-content {
        .form-inline {
          border: 1px solid $dark-body-background;
        }

        .files {
          .file-box {
            border: 1px solid rgba($primary-color, 0.15);
            background-color: $dark-body-background;

            .file-top {
              background-color: $dark-card-background;
              border: 1px solid rgba($primary-color, 0.15);
            }
          }
        }

        .folder .folder-box {
          border: 1px solid rgba($primary-color, 0.15);
          background-color: $dark-body-background;
        }
      }

      .file-sidebar {
        .pricing-plan {
          border: 1px solid rgba($primary-color, 0.15) !important;
        }

        .btn-light {
          color: $dark-all-font-color;
          background-color: $dark-body-background !important;
          border: 1px solid rgba($primary-color, 0.15) !important;
        }
      }

      #lnb {
        border-right: 1px solid $dark-card-border;
      }

      .lnb-new-schedule,
      .lnb-calendars>div {
        border-bottom: 1px solid $dark-card-border;
      }

      #menu .sidebar-list .btn-default {
        color: rgba($dark-all-font-color, 0.2);
      }

      .tui-full-calendar-timegrid-hour {
        background-color: $dark-card-background;
        color: $white !important;
      }

      .tui-full-calendar-timegrid-gridline {
        border-bottom: 1px solid $dark-card-border !important;
      }

