          td {
            &:hover {
              background-color: $dark-card-background;
            }

            span {
              &:hover {
                background-color: $dark-card-background;
              }
            }

            &.prev,
            &.next {
              &:hover {
                background-color: $dark-card-background;
              }
            }
          }

          &:first-child {
