    &.dark-sidebar {
      .toggle-sidebar {
        svg {
          stroke: var(--theme-deafult);
        }
      }

      .page-wrapper {
        .sidebar-main-title {
          p {
            color: $dark-all-font-color;
          }
        }

        &.compact-wrapper {
          .page-body-wrapper {
            div.sidebar-wrapper {
              background-color: $dark-card-background;

              .sidebar-main {
                .sidebar-links {
                  li {
                    a {
                      span {
                        color: $dark-all-font-color;
                      }

                      svg {
                        stroke: $dark-all-font-color;
                      }
                    }
                  }

                  .simplebar-wrapper {
                    .simplebar-mask {
                      .simplebar-content-wrapper {
                        .simplebar-content {
                          >li {
                            .sidebar-link {
                              &.active {
                                background-color: rgba($primary-color, 0.2);

                                svg {
                                  color: var(--theme-deafult);
                                  stroke: var(--theme-deafult);
                                }
                              }
                            }

                            .mega-menu-container {
                              .mega-box {
                                .link-section {
                                  .submenu-title {
                                    h5 {
                                      color: $dark-all-font-color;
                                    }
                                  }

                                  .submenu-content {
                                    &.opensubmegamenu {
                                      li {
                                        a {
                                          color: $dark-all-font-color;
                                          font-weight: 400;
                                        }
                                      }
                                    }
                                  }
                                }
                              }

                              &::after {
                                background-color: $light-all-font-color;
                              }
                            }

                            .sidebar-submenu {
                              li {
                                a {
                                  color: $dark-all-font-color;
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }

                  .sidebar-list {
                    ul.sidebar-submenu {
                      li {
                        a {
                          span {
                            color: $dark-all-font-color;
                          }

                          &.active {
                            color: var(--theme-deafult);
                          }
                        }
                      }
                    }
                  }
                }
              }
            }

            .according-menu {
              i {
                color: $dark-all-font-color;
              }
            }
          }
        }

        &.horizontal-wrapper {
          .page-body-wrapper {
            .sidebar-wrapper {
              background: $dark-card-background;

              .sidebar-main {
                background-color: $dark-card-background;

                .sidebar-links {
                  .simplebar-wrapper {
                    .simplebar-mask {
                      .simplebar-content-wrapper {
                        .simplebar-content {
                          >li {
                            >a {
                              color: $dark-all-font-color;
                            }

                            .sidebar-submenu {
                              background: $dark-card-background;

                              li {
                                a {
                                  color: $dark-all-font-color;

                                  &.active,
                                  &:hover {
                                    color: var(--theme-deafult);
                                  }
                                }

                                .nav-sub-childmenu {
                                  background: $dark-card-background;
                                }
                              }
                            }

                            .mega-menu-container {
                              background: $dark-card-background;

                              .mega-box {
                                .link-section {
                                  .submenu-content {
                                    li {
                                      a {
                                        color: $dark-all-font-color;
                                      }
                                    }
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }

                .left-arrow,
                .right-arrow {
                  background-color: $dark-card-background;
                  color: $dark-all-font-color;
                }
              }
            }
          }
        }

        &.compact-sidebar {
          .page-body-wrapper {
            div {
              &.sidebar-wrapper {
                >div {
                  background: $dark-card-background;
                }

                .sidebar-main {
                  .sidebar-links {
                    li {
                      .sidebar-title {
                        border-bottom: 1px solid $dark-card-border;
                      }

                      a {
                        span {
                          color: $dark-all-font-color;
                        }

                        svg {
                          stroke: $dark-all-font-color;
                        }
                      }

                      .sidebar-submenu,
                      .mega-menu-container {
                        background-color: $dark-card-background;

                        li {
                          a {
                            border-bottom: 1px solid $dark-card-border;
                          }
                        }
                      }
                    }

                    .simplebar-wrapper {
                      .simplebar-mask {
                        .simplebar-content-wrapper {
                          .simplebar-content {
                            >li {
                              .sidebar-link {
                                &.active {
                                  background-color: rgba($primary-color, 0.2);

                                  span {
                                    color: var(--theme-deafult);
                                  }

                                  svg {
                                    color: var(--theme-deafult);
                                    stroke: var(--theme-deafult);
                                  }
                                }
                              }

                              .mega-menu-container {
                                .mega-box {
                                  .link-section {
                                    .submenu-title {
                                      h5 {
                                        color: $dark-all-font-color;
                                      }
                                    }

                                    .submenu-content {
                                      &.opensubmegamenu {
                                        li {
                                          a {
                                            color: $dark-all-font-color;
                                            font-weight: 400;
                                          }
                                        }
                                      }
                                    }
                                  }
                                }

                                &::after {
                                  background-color: $light-all-font-color;
                                }
                              }

                              .sidebar-submenu {
                                li {
                                  a {
                                    color: $dark-all-font-color;
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }

                    .sidebar-list {
                      ul.sidebar-submenu {
                        li {
                          a {
                            span {
                              color: $dark-all-font-color;
                            }

                            &.active {
                              color: var(--theme-deafult);
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }

            .according-menu {
              i {
                color: $dark-all-font-color;
              }
            }
          }
        }
      }
    }

    .for-dark {
      display: none;
    }

    &[class*="dark-"] {
      .for-dark {
        display: block;
      }

      .for-light {
        display: none;
      }
    }



