/**=====================
     07. Color CSS Start
==========================**/
.colors-palette-section {
 > .card-body {
    margin-bottom: -30px;
   .card{
      .card-body{
        position: relative; 
        padding-bottom: 48px;
        h5{
          margin-bottom: 0;
        }
        div.color-name{
          background-color: $white;
          position: absolute;
          bottom: -2px;
          right: 0;
          left: 0;
          text-align: center;
          border-radius: 8px;
          h6{
            padding: 4px;
            margin: 0;
            color: $dark;

          }
        }
      }
    }
   }
 }





.color-box {
  margin-bottom: -10px;

  button {
    margin-bottom: 10px;

    + button {
      margin-left: 5px;
    }
  }
}

// primary Color
$m-val: 13;
$or: $primary-color;

@while $m-val>=1 {
  .primary-color ul {
    display: flex;
    align-items: center; 
    flex-direction: row-reverse;
    justify-content: flex-end;
      

    li {
      width: 100px;
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      &:nth-child(#{$m-val}) {
        $or: lighten($or, 1.5%);
        background-color: $or;
       
       

        span:before {
          content: "#{$or}";
          display: block;
          color: $white;
          text-align: center;
        }

        $m-val: $m-val - 1;
      }
    }
  }
}

//primary Color

