/**=====================
    78. Sidebar CSS Start
==========================**/
%sidebar-after {
  position: absolute;
  content: "";
  top: 48%;
  width: 6px;
  border-top: 2px solid $light-1;
}

%horizontal-after {
  position: absolute;
  content: "";
  top: 49%;
  width: 5px;
  height: 1px;
  border-radius: 100%;
  background-color: #59667a;
}

%common {
  border-left-color: $dark-color;
  transition: $sidebar-transition;
}

%for-animated-hover {
  box-shadow: $main-header-shadow;
  transform: translateY(30px);
  opacity: 0;
  visibility: hidden;
  left: 0;
}

%for-animated-hover-box {
  opacity: 1;
  transform: translateY(0px);
  visibility: visible;
  border-radius: 5px;
  overflow: hidden;

  &:before {
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 7px solid $white;
    content: "";
    top: -7px;
    position: absolute;
    left: 10px;
    z-index: 2;
  }

  &:after {
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 7px solid $light-background;
    content: "";
    top: -7px;
    position: absolute;
    left: 10px;
    z-index: 1;
  }
}
%sidebar-icon {
  display: block;
  transform: rotateX(0deg);
  z-index: 1;
  background-color: $white;
  color: $light-background;
  font-size:14px;
  opacity:1;
  margin-top:-70px;
  padding:10px 0;
  text-align:left;
  box-shadow:0 0 10px rgba(0, 0, 0, 0.13);
  position: absolute;
  width: 245px;
  margin-left: 115px;
}

.page-wrapper {
  position: relative;  
  .page-header {
    max-width:100vw;
    position:$main-header-position;
    top:$main-header-top;
    z-index:$main-header-z-index;
    transition:$sidebar-transition;
    background-color:$white;
    
    .header-wrapper {
      width: 100%;
      display: flex;
      align-items: center;
      padding: $header-wrapper-padding;
      position: relative;
      background-color: $light-2;
  
      .nav-right {
        justify-content: flex-end;

        ul {
          li {
            svg {
              vertical-align: $header-wrapper-nav-icon-align;
              width: $header-wrapper-nav-icon-size;

              path {
                color: $header-wrapper-nav-icon-color;
              }
            }
          }
        }

        &.right-header {
          ul {
            li {
              .mode {
                cursor: pointer;
                width: 20px;
                text-align: center;

                i {
                  font-size: 18px;
                  font-weight: 600;
                  transition: all 0.3s ease;
                  vertical-align: middle;

                  &.fa-lightbulb-o {
                    font-weight: 500;
                    font-size: 22px;
                    transition: all 0.3s ease;
                    animation: zoomIn 300ms ease-in-out;
                  }
                }
              }

              .header-search {
                cursor: pointer;
              }

              .profile-media {
                .media-body {
                  margin-left: 15px;

                  span {
                    font-weight: 500;
                  }

                  p {
                    font-size: 12px;
                    line-height: 1;
                    color: rgba($theme-body-font-color, 0.7);
                  }
                }
              }
            }
          }
        }

        >ul {
          display: flex;
          align-items: center;
          justify-content: flex-end;

          >li {
            padding: 6px 10px;
            margin-right: 5px;
            display: inline-block;
            position: relative;

            .badge {
              position: absolute;
              right: -5px;
              top: -6px;
              padding: 2px 4px;
              font-size: 11px;
              font-weight: 700;
              font-family: work-Sans, sans-serif;
            }

            span {
              ul {
                left: inherit;
                right: -10px;
                width: 130px;

                &:before,
                &:after {
                  left: inherit;
                  right: 10px;
                }

                li {
                  display: block;

                  a {
                    font-size: 14px;
                    color: $dark-color;

                    i {
                      margin-left: 10px;
                      font-size: 13px;
                      color: $dark-color;
                    }
                  }
                }
              }
            }
          }

          .flag-icon {
            font-size: 16px;
          }
        }

        .resp-serch-input{

          .resp-serch-box{
            font-size: 18px ;
            position: relative;
          }
          .search-form{
            top: 60px;
            position: absolute;
            -webkit-transition: all linear 0.3s;
            transition: all linear 0.3s;
            right: 0;
            -webkit-transform: translateY(-35px) scaleY(0);
            transform: translateY(-35px) scaleY(0);
            opacity: 0;
            visibility: hidden;
            width: 180px;
            margin-left: 12px;
           
            &.open{
            
              transform: translateY(0px) scaleY(1);
              opacity: 1;
              visibility: visible;
              transition: all linear 0.3s;
            }
            input{
              padding: 10px;
              background-color: $light-2;
            }
        
          }
        }
        

        .bookmark-flip {
          width: unset !important;
          box-shadow: unset !important;
          background-color: transparent !important;
          overflow: initial;
          &::after, &::before {
            display: none;
          }

          .bookmark-dropdown {
            .bookmark-content {
              text-align: center;
              .bookmark-icon {
                width: 52px;
                height: 52px;
                border: 1px solid $light-gray;
                border-radius: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 auto;
              }
              span {
                font-family: $font-roboto;
                font-size: 13px;
                font-weight: 400;
                letter-spacing: 0.5px;
              }
              &:hover{
                .bookmark-icon {
                    svg{
                      
                          stroke: var(--theme-deafult);
                       
                        
                    }
                }
                span{
                  color: var(--theme-deafult);
                }

              }
             
            }
          }
        }

        .flip-card {
          width: 300px;
          height: 260px;
          background-color: transparent;

          .flip-card-inner {
            position: relative;
            height: 100%;
            text-align: center;
            transition: transform 0.6s;
            transform-style: preserve-3d;

            .front,
            .back {
              position: absolute;
              backface-visibility: hidden;
              border-radius: 10px;
              overflow: hidden;
              box-shadow: 0 0 20px rgba(89, 102, 122, 0.1);
              width: 300px;
              height: auto;

              button {
                background-color: $transparent-color;
                color: $primary-color;
                border: none;

                &:focus {
                  outline: $transparent-color;
                }
              }
            }

            .front {
              background-color: $white;
              transition: .3s;

              svg {
                stroke: $dark-color;
                
              }
            }

            .back {
              background-color: $white;
              transform: rotateY(180deg);

              li {
                &:first-child {
                  margin-top: 20px;
                  height: 140px !important;
                }
              }

              button {
                width: 100%;
              }

              .flip-back-content {
                input {
                  border-radius: 25px;
                  padding: 6px 12px;
                  width: 100%;
                  border: 1px solid #ddd;
                  &:focus {
                    outline: none;
                  }
                }
              }
            }

            &.flipped {
              transform: rotateY(180deg);

              .front {
                opacity: 0;
              }
            }
          }
        }

        .onhover-show-div{
          width: 380px;
          top: 50px;
          right: 0;
          left: unset;
          .dropdown-title {
            padding: 20px;
            border-bottom: 1px solid rgba($dropdown-border-color, 0.3);
            border-top: 5px solid var(--theme-deafult);
              h6 {
                display: inline-block;
              }
           
          }
          ul {
            padding: 15px;
            
          }
          li {
            padding: 15px;
            
            overflow: hidden;
          
       
            p {
              font-size: 14px;
              letter-spacing: 0.3px;
              margin-bottom: 0;
            }
            a {
            
              text-transform: uppercase;
              width: 100%;
              letter-spacing: 0.3px;
              font-family: $font-roboto, $font-serif;
            }

            &+li {
                margin-top: 15px;
            }



            &:last-child {
              padding-bottom: 15px;
            }

            .media {

              position: relative;
              .notification-img {
                
                width: 110px;
                height: 110px;
                background-color: rgba($primary-color, 0.07);
                display: flex;
                align-items: center;
                position: absolute;
                left: -20%;
                justify-content: flex-end;
                border-radius: 50%;
                padding: 10px;
                img {
                  width: 40px;
                  
  
                
                }
              }

             

              .status-circle {
                left: 0;
              }

              .media-body {
                >span {
                  letter-spacing: 0.8px;
                  padding-right: 10px;
                  display: inline-block;
                }

                p {
                  margin-bottom: 8px;
                }
              }
              .notification-right{
                span{
                  font-weight: 200;
                  font-size: 12px;

                }
              }
            }
          }
s
          &:before,
          &:after {
            right: 10px !important;
            left: unset !important;
          }
        }

        .notification-box {
          position: relative;
            .notify-ani{
              animation: tada 1.5s ease infinite;
            }
        }
       
        .cart-nav{
          .cart-box {
            position: relative;
        
          }
  
          .cart-dropdown {
            ul {
              padding: 0 0 0;
            }
            li {
              margin: 0 15px;
              padding: 10px 0;
              padding: 20px 0;
              .media {
                align-items: center;
                .media-body {
                  margin-right: 20px!important;
                  h6{
                    font-size: 14px;
                    margin-bottom: 0;
                  }
                  p{
                    margin: 3px 0;
                    font-size: 14px;
                  }
                  span{
                    font-size: 12px;
                  }
               
                }
                .cart-quantity {
                  width: 90px;
                  align-items: center;
                  display: flex;
                  justify-content: center;
                  padding: 4px 15px;
                  border-radius:  $card-border-radious;
                  border:$card-border-color;
  
                  span{
                    font-size: 12px;
                    color: var(--theme-deafult);
                  }
                  input{
                    max-width: 65%;
                    text-align: center;
                    border: none;
                    outline: none;  
                    background-color: transparent;
                    &:focus{
                      outline: none;
                      border: none;
                    }
                  }
                }
              }
  
              &+li {
                margin-top: 0;
                border-top: 1px solid rgba($dropdown-border-color, 0.1);
              }
              &:last-child {
                
             
                border-top: none;
              }
              &:hover{
                .media{
                  .close-circle{
                    opacity: 1;
                    visibility: visible;
                    transition: all ease-in-out .5s;
                  }
  
                }
              }
            }
  
            p {
              line-height: 1.3;
              margin-top: 3px;
              opacity: 0.6;
            }
  
            .close-circle {
              position: absolute;
              right: 0;
              top: -10px;
              opacity: 0;
              visibility: hidden;
              transition: all ease-in-out .5s;
            
              a {
                width: 18px;
                height: 18px;
                display: flex;
                align-items: center;
                justify-content: center;
              }
  
              svg {
                width: 14px;
                height: 14px;
                stroke: #9c9c9c;
              }
            }
         
            .cupon{
              text-align: center;
              padding: 10px 0;
              input{
                width: 75%;
                text-align: center;
                margin: auto;
                padding: 10px 0;
                border: none;
                border-bottom: 1px solid;
              }
            }
            .order-total{
              display: flex;
              align-items: center;
              justify-content: space-between;
              .view-checkout {
                width: auto;
              }
            }
           
          }
        }

        .notification-dropdown {
         

          li {
            border: $card-border-color;
            border-radius: 5px;
            
            
           
             
            .media{
              align-items: center;
              .media-body{
                white-space: nowrap;
                margin-left: 60px;

                p {
                  margin:0;
                  color: gray;
                  font-size: 12px;
                }
                h5{
                  font-size: 16px;
                  margin: 3px 0;
                }
                a{
                  font-size: 12px;

                  span{
                    padding-left: 5px;
                  }
                }

              }
              a{
                font-size: 10px;
                margin-left: 12px;
                color: $black;
              }
            }

          }

          &:before,
          &:after {
            right: 28px !important;
            left: unset !important;
          }
        }

   
        .profile-nav{
          .media{
            align-items: center;
          }
        }

        .profile-dropdown {
          width: 160px;
          top: 57px;
          left: -12px;
          padding: 0 10px;

          li {
            padding: 10px !important;
            border-bottom: 1px solid $light-gray;
            &:last-child{
              border: none;
            }
            a {
              text-decoration: unset;
            }

            &:hover {
              a {
                span {
                  color: var(--theme-deafult);
                  transition: color 0.3s ease;
                }

                svg {
                  stroke: var(--theme-deafult);
                  transition: stroke 0.3s ease;

                  path {
                    stroke: var(--theme-deafult);
                    transition: stroke 0.3s ease;
                  }
                }
              }
            }
             + li {
               margin-top: 0;
             }

            &:last-child {
              padding-top: 10px !important;
            }

            span {
              color: $dark-color;
              font-size: 13px;
            }

            svg {
              width: 16px;
              vertical-align: bottom;
             
              stroke: $dark-color;
              float: right;
            }
          }
        }
      }
    }
  }

  .page-body-wrapper {
    background-color: $body-color;

    .page-body {
      min-height: calc(100vh - #{$header-size});
      margin-top: $header-size;
      padding: $page-body-padding;
      position: relative;
      transition: 0.5s;

      &:before {
        animation: fadeIn 0.5s ease-in-out;
      }
    }

    .page-title {
      padding: $page-title-padding;
      background-color: transparent;
      
      >.row {
        margin: 0;
        .col-12{
          padding: 0 ;
          &:first-child {
            display: flex;

            h3 {
              padding-right: 20px;
            }
          }
        }
      }

      .row {
        align-items: center;

        h3 {
          font-size: $page-title-font-size;
          margin-bottom: $page-title-margin-bottom;
          text-transform: $page-title-text-tranform;
          font-weight: 600;

          small {
            display: $page-small-title-display;
            font-size: $page-small-title-font-size;
            margin-bottom: $page-small-title-margin-bottom;
            text-transform: $page-small-title-text-transform;
            color: $theme-body-sub-title-color;
          }
        }
      }

      .breadcrumb {
        background-color: $transparent-color;
        padding: 0;
        margin-bottom: 0;
        align-items: center;
        justify-content: flex-end;
        color: var( --theme-deafult);

        li {
          font-size: 14px;
          letter-spacing:0.5px;
          font-family: $font-roboto;
          font-weight: 500;
          line-height: 16.41px;
          
        }

        .breadcrumb-item {
          color: var(--theme-deafult);
       
          &+.breadcrumb-item {
            &:before {
              content: $breadcrumb-content;
              font-weight: 500;
              color: var( --theme-deafult);
            }
          }

          a {
            padding: 6px 7px;
            background-color: $light-2;
            border-radius: 5px;
            svg {
              width: 16px;
              height: 16px;
              vertical-align: text-top;
            }
          }

         

          &.active{
            font-weight: 300;
          
          }
        }
      }
    }
  } 

  &.horizontal-wrapper {
    [data-simplebar] {
      position: relative;
    }
    .footer {
      margin-left: 0;
    }

    .header-logo-wrapper {
      width: 150px;

      .toggle-sidebar {
        display: none;
      }
    }

   

   > .page-body-wrapper {
      li {
        display: inline-block;
      }


      .sidebar-wrapper {
        height: 54px;
        line-height: inherit;
        top: 94px;
        background: $white;
        box-shadow: 0px 9px 24px rgba($semi-dark, 0.05);
        overflow-x: hidden;
        z-index: 1;
        overflow-y: visible;
        position: fixed;
        width: 100vw;
        border-top: 1px solid $light-semi-gray;

        .badge {
          position: absolute;
          top: 2px;
          right: 0px;
          z-index: 1;
        }

        .logo-wrapper,
        .logo-icon-wrapper {
          display: none;
        }

        .sidebar-main { 
          text-align: left;
          position: relative;

          #sidebar-menu {
            // position: fixed;
            // left: 15px;
            // top: 85px;
          }

          .left-arrow,
          .right-arrow {
            position: absolute;
            top: 0px;
            padding: 15px;
            background-color: $white;
            display: flex;
            align-items: center;
            justify-content: center;
            z-index: 1;
            cursor: pointer;

            &.disabled {
              display: none;
            }
          }

          .left-arrow {
            left: 0;
          }

          .right-arrow {
            right: 10px;
          }

          .sidebar-links {
            list-style: none;
            margin: 0;
            padding: 0;
            line-height: normal;
            direction: ltr;
            text-align: left;
            display: -webkit-box !important;
            padding: 0 5px;
            transition: all .5s ease;
            white-space: nowrap;

            li {
              &.sidebar-list {

                a {
                  &.active {
                    .according-menu {
                      i {
                        &:before {
                          content: '-';
                        }
                      }
                    }
                  }

                  .according-menu {
                    display: none;
                  }
                }

                ul.sidebar-submenu {
                  li {
                    a {                     
                      &.active {
                        color: var(--theme-deafult);

                        &:after {
                          background-color: var(--theme-deafult);
                        }
                      }
                    }
                  }
                }
              }
            }

            .simplebar-wrapper {             
              .simplebar-mask {
                .simplebar-content-wrapper {                 
                  .simplebar-content {
                     >li {
                       position: relative;
                       padding-top: 10px;
                       padding-bottom: 10px;

                       &:hover {
                         >a {
                           >span {
                             color: var(--theme-deafult);
                             transition: all 0.3s ease;
                           }
                         }

                         a {
                           background-color: rgba($primary-color, 0.15%);
                           transition: all 0.3s ease;

                           svg {
                             fill: rgba($primary-color, 0.10%);
                             stroke: var(--theme-deafult);
                             transition: all 0.3s ease;
                           }
                         }
                       }

                       &:first-child {
                         display: none !important;
                       }

                       >a {
                         display: block;
                         color: $theme-body-font-color;
                         line-height: 23px;
                         text-decoration: none;
                         text-transform: capitalize;
                         padding: 5px 15px;
                         margin: 0 3px;
                         letter-spacing: 0.04em;
                         position: relative;
                         border-radius: 5px;
                         font-weight: 500;
                         transition: all 0.3s ease;

                         &.active {
                           //background-image: linear-gradient(90deg, var(--theme-deafult) 0%, #a26cf8 100%);
                           background-color: lighten($primary-color, 22%);
                           transition: all 0.3s ease;

                           svg,
                           span {
                             color: var(--theme-deafult);
                             transition: all 0.3s ease;
                           }
                         }

                         .badge {
                           position: absolute;
                           top: -5px;
                           margin-left: 1px;
                           right: 10px;
                           padding: 0.20em 0.7em;
                         }

                         svg {
                           width: 16px;
                           vertical-align: bottom;
                           margin-right: 10px;
                           stroke-width: 2.5px;
                         }
                       }

                       .sidebar-submenu {
                         position: fixed;
                         // left: 0;
                         z-index: 4;
                         box-shadow: 0 0 1px 0 $dark-gray;
                         padding: 15px 0px 15px 0px;
                         background: white;
                         li {
                           border: 0;
                           float: none;
                           position: relative;
                           display: block;

                           a {
                             background: transparent;
                             padding: 4px 30px;
                             display: block;
                             transition: all 0.5s ease;
                             line-height: 1.9;
                             color: rgba(43, 43, 43, 0.6);
                             letter-spacing: 0.06em;
                             font-weight: 500;
                             font-family: Roboto;
                             transition: all 0.3s ease;

                             &:after {
                               left: 20px;
                               @extend %horizontal-after;
                             }

                             &:hover {
                               color: var(--theme-deafult);
                               letter-spacing: 1.5px;
                               transition: all 0.3s ease;

                               &:after {
                                 background-color: var(--theme-deafult);
                               }
                             }
                           }
                         }
                       }

                       .mega-menu-container {
                         position: fixed;
                         right: 0;
                         left: 0;
                         margin: 0 auto;
                         z-index: 4;
                         box-shadow: 0 0 1px 0 $dark-gray;
                         padding: 30px;
                         background: white;
                         width: 95%;
                         border-radius: 4px;
                         display: block !important;

                         .mega-box {
                           width: 20%;

                           .link-section {
                             .submenu-title {
                               h5 {
                                 text-transform: uppercase;
                                 font-size: 14px;
                                 margin-left: -13px;
                               }
                             }

                             .submenu-content {
                               box-shadow: none;
                               background-color: transparent;
                               position: relative;
                               display: block;
                               padding: 0;

                               li {
                                 border: 0;
                                 float: none;
                                 position: relative;
                                 display: block;

                                 a {
                                   border: 0 !important;
                                   background: transparent;
                                   color: $theme-body-font-color;
                                   border-radius: 0 !important;
                                   text-transform: capitalize;
                                   padding: 3px 0;
                                   font-size: 13px;
                                   display: block;
                                   letter-spacing: 0.07em;
                                   line-height: 1.9;
                                   position: relative;
                                   transition: all 0.3s ease;

                                   &:hover {
                                     color: var(--theme-deafult);
                                     letter-spacing: 1.5px;
                                     transition: all 0.3s ease;

                                     &:after {
                                       color: var(--theme-deafult);
                                       transition: all 0.3s ease;
                                     }
                                   }

                                   &:after {
                                     position: absolute;
                                     content: "";
                                     left: -12px;
                                     top: 50%;
                                     width: 5px;
                                     height: 1px;
                                     background-color: $light-semi-font;
                                   }
                                 }
                               }
                             }
                           }
                         }
                       }

                       &:hover {
                         >a {
                           &:after {
                             left: 0;
                             right: 0;
                             opacity: 1;
                           }
                         }
                       }

                       &.mega-menu {
                         position: unset;
                       }
                     }
                  }
                }
