/**=====================
    24. According CSS start
==========================**/
.default-according {
    .card {
        margin-bottom: 0;
        box-shadow: none;

        &+.card {
            margin-top: $according-card-top-margin;
        }

        .btn-link {
            font-weight: $according-btn-weight;
            color: $theme-body-font-color;
            text-decoration: none;
        }

        .btn-link {

            &:focus,
            &:hover {
                text-decoration: none;
            }
        }

        .card-header {
            padding: $according-card-header-padding;
            border: $card-border-color;

            i {
                position: absolute;
                left: 18px;
                font-size: 20px;
                top: 20px;
            }

            h5 {
                margin-top: 2px;
            }
        }

        .card-body {
            border: $card-border-color;
            border-top: none;
        }
    }
}

.default-according.style-1 {
    button {
        width: 100%;
        text-align: left;

        &:before {
            right: 20px;
            position: absolute;
            -webkit-transition: 0.4s;
            transition: 0.4s;
            font-size: 20px;
            top: 50%;
            -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
        }

        &[aria-expanded="true"] {
            border-radius: 0;
            border-top-left-radius: 5px;
            border-top-right-radius: 5px;

            &:before {
                content: $according-open-icon;
                font-family: $according-card-header-icon;
            }
        }

        &[aria-expanded="false"] {
            border-radius: 10px;

            &:before {
                content: $according-close-icon;
                font-family: $according-card-header-icon;
            }
        }
    }
}

.default-according {
    .card {
        .card-header {

            button.btn-link {
                &[aria-expanded="true"] {
                    border-radius: 0;
                    border-top-left-radius: 5px;
                    border-top-right-radius: 5px;
                }

                &[aria-expanded="false"] {
                    border-radius: 5px;
                }
            }
        }
    }
}

@media only screen and (max-width: 991.98px) {
    .default-according .card .card-header {
        padding: 0!important;
    }
}



/**=====================
     24. According CSS Ends
==========================**/